define('forw/routes/application', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Route.extend({
    queryParams: {
      modal: {
        refreshModel: true
      }
    },
    model: function model() {
      return this.get("currentUser");
    },
    actions: {
      closeModal: function closeModal() {
        this.set("modal", null);
      }
    }
  });

});